<template>
  <div id="baike_pc">
    <PCHeader></PCHeader>

    <section class="nav">
      <PCCommonNav></PCCommonNav>
    </section>

    <section class="main_center">
      <div class="left">
        <section class="baike_detail" style="margin-top:0px;" v-if="data">
          <h3>{{data.title}}</h3>
          <span class="time">发布时间：{{data.create_at|formatDate}}</span>
          <p class="des"><i>文章摘要:</i>{{data.des}}</p>
          <article  class="lj-editor-show" v-html="data.content"></article>
        </section>

      </div>
      <div class="right">

        <section class="category">
          <h3><i>百科</i>分类</h3>
          <div class="item" v-for="item in ask_category">
              <PCBaikeType :itemData="item" @itemAction="baikeTypeAction"></PCBaikeType>
          </div>
        </section>

      </div>
    </section>

    <PCFooter></PCFooter>

  </div>
</template>

<script>
import { Toast,Indicator} from 'mint-ui'
import {getStore,imgBaseUrl} from '../../components/common/mUtils'
import PCHeader from '../../components/header/PCHeader'
import PCFooter from '../../components/footer/PCFooter'
import PCBaikeItem from '../../page/home/children/PCBaikeItem'
import PCBaikeType from '../../page/home/children/PCBaikeType'
import PCCommonNav from '../../components/common/PCCommonNav'
export default {
  data(){
    return{
      imgBaseUrl,
      userToken:'',
      userID:'',
      isLogin:false,
      data:null,
      id:0,
      list:[],
      ask_category:[],
      sid:1,
      typeList:[]
    }
  },
  created(){
    this.userToken = getStore('userToken')
    this.userID = getStore("userID")
    if (this.userID) {
      this.isLogin = true
    }

    // 检测是不是移动端
    this.isMobile()
  },
  filters:{
    getNumber(idx){
      ++idx
      if (idx < 10) {
        idx = '0'+idx
      }
      return idx;
    }
  },
  mounted(){
    this.id = this.$route.query.id

    // 获取热导航
    this.getNav()

    // 获取
    this.getData()

  },
  components:{
    PCHeader,
    PCFooter,
    PCBaikeItem,
    PCBaikeType,
    PCCommonNav
  },
  computed:{
    getUserAvatar:function(){
      if(this.userInfo){
        return imgBaseUrl + this.userInfo.avatarPath
      }else {
        return "http://qpic.07358.com/FiPMqTlrYzi_8naGgKIfj4h9wGLi"
      }
    }
  },
  methods: {
    baikeTypeAction(subItem){
      this.$router.push({path: '/baikeList_pc', query:{sid:subItem.ask_typezi_id,typeTitle:subItem.titleName+'|'+subItem.name}})
    },
    gotoAddress(path,login){
      if (login) {
        if (this.isLogin) {
          this.$router.push(path)
        }else {
          this.$router.push('/login_pc')
        }
      }else {
          this.$router.push(path)
      }
    },
    getData(){
      // Indicator.open('加载中....');
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/showbk?id='+this.id
      // console.log(getUrl)
      this.axios.get(getUrl)
      .then(function (response) {
        let code = response.data.code
        if (code == 200) {
          this.data = response.data.result
          this.title = this.data.title
        }
        // Indicator.close();
      }.bind(this))
      .catch(function (error) {
        console.log(error);
      });
    },
    // 导航分
    getNav(){
      Indicator.open();
      let getUrl = this.CFWConfig.APIHost + '/v3/ask/fu'
      // console.log(getUrl)
      this.$http.get(getUrl).then(response => {
        let code = response.data.code
        if (code == 200) {
          // this.nav = response.data.result
          this.ask_category = response.data.result
        }
        Indicator.close();
      }, response => {
        Toast('请求失败');
        console.log(response);
        Indicator.close();
      });
    },
    isMobile(){
      var sUserAgent = navigator.userAgent;
    	var mobileAgents=['Android','iPhone','Symbian','WindowsPhone','iPod','BlackBerry','Windows CE'];
    	var goUrl = 0;
    	for( var i=0;i<mobileAgents.length;i++){
    			if(sUserAgent.indexOf(mobileAgents[i]) > -1){
    					goUrl = 1;
    					break;
    			}
    	}
      // console.log(goUrl)
    	if (goUrl != 1){
        console.log('pc')
    	}else {
    	  console.log('Mobile')
        this.$router.push('home')
    	}
    }
  },

}
</script>

